
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { payOrder, getPayWays } from '@/api/user'
export default {
name: 'shoppingCard',
components: {
Radio,
Group,
XButton
},
data () {
return {
radio003: [{
icon: '../../../assets/wechat.png',
key: '001',
value: 'radio001'
}],
showtoast: false,
issubscribed: false, // 是否关注
qrcodeurl: '', // 二维码
payInfo: {},
postData: {
orderid: '',
payway: 'weih5'
}
}
},
created () {
const orderid = this.$route.query.id
this.postData.orderid = orderid
const postData = {
orderid: orderid
}
getPayWays(postData)
.then(res => {
if (res.data.errorcode === 0) {

console.log(res)
this.payInfo = res.data.data
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('请求出错！', 'middle')
})
},
methods: {
gohome () {
this.$router.replace({
path: '/home'
})
},

handlePay () {
payOrder(this.postData)
.then(res => {
if (res.data.errorcode === 0) {


if (res.data.data.issubscribed === 0) {
this.issubscribed = false
this.qrcodeurl = res.data.data.qrcodeurl
} else {
this.issubscribed = true
}




if (res.data.data.mweb_url != "")
{//微信H5直接跳转型
window.location.href = res.data.data.mweb_url;
}
else if (res.data.data.aliscript != "")
{//支付宝执行script型
document.write(res.data.data.aliscript);
}
else
{//微信公众号型

let opthions = {
appId: res.data.data.jsApiParameters.appId,
timeStamp: String(res.data.data.jsApiParameters.timeStamp), // 必须为字符串
nonceStr: res.data.data.jsApiParameters.nonceStr,
package: res.data.data.jsApiParameters.package,
signType: res.data.data.jsApiParameters.signType,
paySign: res.data.data.jsApiParameters.paySign
}
console.log(opthions)
console.log(res.data.data.jsApiParameters)

this.jsApiCall(opthions)
}













} else if (res.data.errorcode === 80088) {

const that = this
this.$vux.confirm.show({

content: '支付成功',

confirmText: '查看订单详情',
cancelText: '返回首页',

onCancel () {
that.gohome()
},
onConfirm () {
that.$router.push({path: '/usercenter/orderdetail', query: {id: that.postData.orderid}})
}
})



































} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('请求出错！', 'middle')
})
},
jsApiCall (obj) {
const that = this
WeixinJSBridge.invoke(
'getBrandWCPayRequest',
{
'appId': obj.appId,
'timeStamp': obj.timeStamp,
'nonceStr': obj.nonceStr,
'package': obj.package,
'signType': obj.signType,
'paySign': obj.paySign
},
function (res) {


if (res.err_msg === 'get_brand_wcpay_request:ok') { // 前端显示成功，转向到个人中心订单页



if (!that.issubscribed) {
that.$vux.confirm.show({
title: '支付成功，关注公众号可第一时间获取订单动态',
content: '<img src="' + that.qrcodeurl + '" width="100%"/>',

confirmText: '查看订单详情',
cancelText: '返回首页',

onCancel () {
that.gohome()
},
onConfirm () {
that.$router.push({path: '/usercenter/orderdetail', query: {id: that.postData.orderid}})
}
})
} else {
that.$vux.confirm.show({

content: '支付成功',

confirmText: '查看订单详情',
cancelText: '返回首页',

onCancel () {
that.gohome()
},
onConfirm () {
that.$router.push({path: '/usercenter/orderdetail', query: {id: that.postData.orderid}})
}
})
}







}
}
)
},
